.formControl.error textarea {
  border: 2px solid red;
}

.formControl.error input {
  border: 2px solid red;
}

.formControl {
  display: flex;
}

.formControl.error span {
  color: white;
  font-style: italic;
}

.color {
  background-color: #45484e;
}

.warning {
  background-color: #45484e;
}

.formControl textarea {
  border: 1px solid whitesmoke;
  border-radius: 10px;
  font-size: 20px;
  color: whitesmoke;
  padding: 10px;
  resize: none;
}

.formControl input {
  border: 1px solid whitesmoke;
  border-radius: 10px;
  font-size: 20px;
  color: whitesmoke;
  padding: 10px;
  resize: none;
}

.icon {
  background-color: #45484e;
  margin: 0;
  padding-top: 20px;
}
